import test from 'ava'
import {scan, total} from '../src/checkout'

test('test checkout false scan', t => {
  t.is(scan('abc'), false)
})
test('test checkout true scan', t => {
  t.is(scan('ipd'), true)
})
// test the total
test('test checkout scan is 549.99', t => {
  return total().then(result => {
    t.log(result)
		t.true(result === 549.99)
	})
})